import request from '@/plugin/axios';
export default {
  name: 'menu-list',
  data: function data() {
    return {
      filename: __filename,
      loading: true,
      query: {
        title: ''
      },
      create: {
        id: '',
        title: '',
        path: '',
        icon: '',
        parentId: '',
        level: '1'
      },
      pagination: {
        currentPage: 1,
        pageSize: 20,
        pageSizes: [10, 20, 30, 50, 100],
        total: 0
      },
      tableData: [],
      formLabelWidth: '80px',
      menuOption: [],
      dialogFormVisible: false
    };
  },
  created: function created() {
    this.getTableData();
    this.getMenuOption('1');
  },
  watch: {
    'pagination.currentPage': function paginationCurrentPage(val) {
      this.getTableData();
    },
    'pagination.pageSize': function paginationPageSize(val) {
      this.getTableData();
    },
    'create.level': function createLevel(val) {
      this.getMenuOption(val); // 只有新建才清空

      if (this.create.id === '') {
        this.create.parentId = '';
      }
    }
  },
  methods: {
    handleEdit: function handleEdit(row) {
      var _this = this;

      if (row.id) {
        this.create.id = row.id;
        request.get("/menu/".concat(row.id)).then(function (res) {
          _this.create = res.data;
        });
        this.dialogFormVisible = true;
      }
    },
    search: function search() {
      this.pagination.currentPage = 1;
      this.getTableData();
    },
    addMenu: function addMenu() {
      // 清空数据
      this.create.id = '';
      this.dialogFormVisible = true;
      this.$refs['create'].resetFields();
    },
    reset: function reset(formName) {
      this.$refs[formName].resetFields();
      this.getTableData();
    },
    handleSizeChange: function handleSizeChange(val) {
      this.pagination.pageSize = val;
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.pagination.currentPage = val;
    },
    getTableData: function getTableData() {
      var _this2 = this;

      this.loading = true;
      var page = this.pagination;
      var params = {
        'index': page.currentPage,
        'size': page.pageSize
      };
      var filters = {
        'title': this.query.title
      };
      params.filters = filters;
      request.get('/menu', {
        params: params
      }).then(function (res) {
        _this2.pagination.total = res.data.count;

        if ((_this2.pagination.currentPage - 1) * _this2.pagination.pageSize >= _this2.pagination.total) {
          _this2.pagination.currentPage--;
          return;
        }

        _this2.tableData = res.data.list;
        _this2.loading = false;
      }).catch(function () {
        _this2.loading = false;
      });
    },
    getMenuOption: function getMenuOption(level) {
      if (level === '1') {
        this.menuOption = [];
      } else {
        this.getMenuOptionByLevel(parseInt(level) - 1);
      }
    },
    getMenuOptionByLevel: function getMenuOptionByLevel(level) {
      var _this3 = this;

      if (level) {
        request.get("/menu/level/".concat(level)).then(function (res) {
          _this3.menuOption = res.data;
        });
      }
    },
    save: function save() {
      var _this4 = this;

      var params = JSON.parse(JSON.stringify(this.create));
      request.post('/menu', params).then(function (res) {
        if (res.code === 0) {
          _this4.dialogFormVisible = false;

          _this4.$refs['create'].resetFields();

          _this4.getTableData();
        }
      });
    },
    deleteMenu: function deleteMenu(row) {
      var _this5 = this;

      request.delete("/menu/".concat(row.id)).then(function (res) {
        if (res.code === 0) {
          _this5.getTableData();

          _this5.$message({
            message: '删除成功',
            type: 'success'
          });
        }
      });
    }
  }
};